import React, { Component } from 'react';
import { Navbar } from 'react-bootstrap';
class NavBar extends Component {
    render() {
        let url = "";
        return (

            <Navbar expand="lg" className={"navbar navbar-expand-lg mt-1  fixed-top navbar-dark"}>
                <div className="container">
                    <a className="navbar-brand" href={url} reload="true">Mehdi</a>
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    <Navbar.Collapse id="basic-navbar-nav">

                        <ul className="navbar-nav ml-auto mt-2 mt-lg-0" >
                            <li className="nav-item">
                                <a className="nav-link" href="#intro" >INTRO</a>
                            </li>

                            <li className="nav-item">
                                <a className="nav-link" href="#about-me">ABOUT ME</a>
                            </li>

                            <li className="nav-item">
                                <a className="nav-link" href="#services">SERVICES</a>
                            </li>

                            <li className="nav-item">
                                <a className="nav-link" href="#work">WORK</a>
                            </li>

                            <li className="nav-item">
                                <a className="nav-link" href="#client">CLIENT</a>
                            </li>

                            <li className="nav-item">
                                <a className="nav-link" href="#contact">CONTACT</a>
                            </li>
                        </ul>

                    </Navbar.Collapse>
                </div>
            </Navbar>
        )
    }
}
export default NavBar;
