import React, { Component } from 'react';
import NavBar from './NavBar';
import Typed from "react-typed";

class Header extends Component {
    render() {
        return (
            <header id="intro">
                <NavBar />
                <section className="section-1">
                    <div className="personal-section-1">
                        <img src="Img/mehdi-vahadani.jpg" alt="lap top " data-aos={"fade-in"} data-aos-duration={"2000"} />
                        <h1>Hi, I'm Mehdi Vahdani</h1>
                        <h2 style={{ color: '#bfbfbf' }}>i'm
                        <span className="typer-1">
                                <Typed strings={['front-end developer .', 'hard worker .', 'creative .']} typeSpeed={90} backSpeed={90} loop ></Typed>
                            </span>
                        </h2>
                    </div>

                    <div className="mouse">
                        <div className="mouse-outline">
                            <div className="mouse-dot"></div>
                        </div>
                    </div>
                </section>
            </header>
        )
    }
}

export default Header;